body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: "Open Sans", Helvetica, sans-serif;
  background-color: white;
  max-width: 1032px;
  position: relative;
  margin-left: auto;
  margin-right: auto;
}

.App-header {
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.logo {
  font-size: 1.2em;
  margin-bottom: 4vh;
  margin-top: 4vh;
}

a.logo:link {
  color: #6C6C6C;
  text-decoration: none;
}

a.logo:visited {
  color: #6C6C6C;
  text-decoration: none;
}

a.logo:hover {
  color: #6C6C6C;
  text-decoration: none;
}

a.logo:active {
  color: #6C6C6C;
  text-decoration: none;
} 

.bannerImg {
  width: 100%;
  max-width: 1032px;
}

.redRect {
  position: absolute;
  float: left;
}

.blueRect {
  position: absolute;
  float: left;
}

#one{
  left: 18.5%;
  top: 370px;
}

#two {
  left: 30%;
  top: 350px;
}

#three {
  left: 47%;
  top: 377px;
}

#four {
  left: 56%;
  top: 375px;
}

#five {
  left: 86%;
  top: 370px;
}

.map {
  margin-top: 20px;
  max-width: 1070px;
}


.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.card{
    background-color: white;
    border: .25px solid black;
    box-shadow: 5px 4px 18px #FFD6D6;
    padding: 1%;
    border-radius: 10pt;
    height: 180px;

}

.picture {
    float: left;
    padding-right: 5%;
    padding-left: 1.4%;
    padding-top: 1.2%;

}

.about {
    float: left;
    width: 48%;
    
}

.city {
    color: #4E88D6;
    font-size: 12px;
    margin-top: 15px;
    margin-bottom: 5px; 
}

.landmark {
    font-weight: 600;
    font-size: 18px;
    letter-spacing: 1px;
    margin-bottom: 5px;
    margin-top: 8px;
}

.description {
    font-weight: 300;
    font-size: 14px;
    letter-spacing: .3px;
    line-height: 18px;
    margin-top: 8px;
}
.box {
    height: 380px;
    width: 200px;
    float: left;
    margin-left: 10px;
    padding-left: 15px;
    padding-right: 15px;
    margin-right: 10px;
    width: 20%;

    box-shadow: 4px 4px 18px #ccc;
    margin-bottom: 20px;
}

.money {
    color: #4E88D6;
    font-size: 12px;
    margin-top: 15px;
    margin-bottom: 5px; 
}

.foodname{
    font-weight: 600;
    font-size: 18px;
    letter-spacing: 1px;
    margin-bottom: 5px;
    margin-top: 8px;
}

.des {
    font-weight: 300;
    font-size: 14px;
    letter-spacing: .3px;
    line-height: 18px;
    margin-top: 8px;
}
